html, body {
    height: 100%; }
body {
    background: #fafcfd url(../../images/lari/login-bg.png) no-repeat center center;
    font-size: 13px;
    font-family: "Roboto", Helvetica, Arial, sans-serif;
    font-weight: 500;
    text-rendering: optimizeLegibility;
    font-smooth: always;
    font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
    display: flex;
    align-items: center;
    justify-content: center; }

h1 {
    font-size: 18px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 20px;
    text-align: center; }
a {
    text-decoration: none; }
* {
    font-weight: 500;
    margin: 0;
    padding: 0; }
.LoginForm {
    background: #ffffff;
    border-radius: 6px;
    box-shadow: 0px 1px 46px 0px rgba(211, 229, 238, 1);
    padding: 30px;
    width: 310px; }
.InputGroup {
    padding: 10px 0;
    position: relative;
    label {
        color: #343434;
        font-size: 14px; } }

.Input {
    border: 2px solid #e5e7ef;
    border-radius: 50px;
    box-sizing: border-box;
    color: #343434;
    font-size: 14px;
    padding: 10px 20px;
    width: 100%; }
.Input--Checkbox {
    padding-bottom: 20px;

    label {
        display: flex;
        align-items: center;
        cursor: pointer; }
    label:before {
        border: 2px solid #e5e7ef;
        border-radius: 2px;
        content: " ";
        display: inline-block;
        margin-right: 10px;
        height: 16px;
        width: 16px;
        transition: all .5 linear; }
    input[type="checkbox"] {
        display: none;
        &:checked + label:before {
            background: url(../../images/lari/icon-check.png) no-repeat center center; } } }

.ShowPassword {
    background: url(../../images/lari/icon-show-password.png) no-repeat center center;
    cursor: pointer;
    display: block;
    height: 18px;
    width: 18px;
    position: absolute;
    right: 20px;
    top: 20px; }
.ShowPassword--active {
    background: url(../../images/lari/icon-show-password-active.png) no-repeat center center; }

.Button {
    background: #3174e1;
    border: 0;
    border-radius: 50px;
    color: #ffffff;
    cursor: pointer;
    font-size: 14px;
    padding: 10px 0;
    text-align: center;
    width: 100%; }

.Button, .Input {
    &:focus, &:active {
        outline: none; } }
.Input--Error {
    border-color: #f44336; }

.Forgot {
    padding-top: 10px;
    text-align: center;
    a {
        color: #a2a7b0; } }
.Lead {
    color: #a2a7b0;
    line-height: 1.5em;
    padding-bottom: 20px;
    text-align: center; }

.Error {
    color: #f44336;
    padding: 7px 0 0 20px;
    &:empty {
        display: none; } }

.Success {
    color: #8BC34A;
    font-size: 15px;
    padding-bottom: 10px;
    text-align: center; }
